.process-missing-message {
    margin-top: 5em;
}

.rendered-processes-container {
    margin-top: 4em;
}

.rendered-processes {
    margin-top: 1em;
    display: flex;
    width: 100%;
}

.process {
    min-width: 40px;
    position: relative;
    border: 1px solid black;
}

.process-from-to {
    position: absolute;
    bottom: -25px;
    left: 0;
    right: 0;
    font-size: 9px;
    font-family: monospace;
}